.card {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: $cardWidth;
  height: $cardHeight;

  padding: 16px 14px 14px;
  
  border: 1px solid $outerBorderColor;
  border-radius: 5px;

  &__image {
    background-image: url(/images/imac.jpeg);
    background-size: cover;
    background-position: center;

    height: 134px;
    width: 160px;

    &-container {
      margin: 0 5px 40px;
      padding: 14px 0 5px;
      transition: transform 1s;

      &:hover {
        transform: scale(1.2);
        outline: 3px solid $outerBorderColor;
      }
    }
  }
  
  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    margin: 0 0 5px;
  }
  
  &__code {
    @include textFont();
    align-self: flex-start;
  }
  
  &__rate {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: 15px;

    margin: 15px 0 20px;
    
    .card__feedback {
      @include textFont();
    }
  }
  
  &__price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    width: 100%;

    margin: 0 0 20px;

    .card__cost {
      @include textFont();
    }

    .card__sum {
      font-weight: bold;
      font-size: 16px;
      line-height: 19px;
    }
  }

  &__button {
    font-weight: bold;
    font-size: 14px;
    line-height: 40px;

    text-align: center;
    text-transform: uppercase;

    color: $main-color;
    background: $element-color;

    width: 170px;
    height: 40px;

    border-radius: 5px;
    border: 1px solid $element-color;

    transition: background-color 1s, color 1s;

    &:hover {
      background-color: $main-color;
      color: $element-color;
    }
  }
}
